@import '../../styles/mixins';

.cart {
  @include flex(column, flex-start, center);
  width: 350px;
  color: var(--color-light);
  line-height: 1.5rem;

  .img-wrap {
    width: 300px;
    height: 400px;

    img {
      @include img;
    }
  }

  &:nth-child(2) {
    background-color: #43768b;
    .title-cart {
      background-color: #6991a2;
    }
  }

  &:nth-child(3) {
    background-color: #7bc144;
    .title-cart {
      background-color: #95cd69;
    }
  }

  button {
    background-color: transparent;
    border: 2px solid var(--color-light);
    padding: 0.2rem;
    margin: 0 0 1.5rem 1.5rem;
    width: 150px;

    &:hover {
      color: var(--color-light);
      background-color: #222222;
    }
  }

  @include sm-max {
    width: auto;
  }

  .title-cart {
    text-transform: uppercase;
    font-family: var(--header-font);
    font-size: var(--font-dubtitle);
    letter-spacing: 1px;
    font-weight: var(--semi-bold);
    color: var(--color-light);
    width: 100%;
    background-color: #222222;
    padding: 1rem;
  }

  .text-cart {
    text-align: left;
    padding: 2rem;
    // border-left: 2px solid var(--1);
    // border-right: 2px solid var(--1);
  }
}
