@import '../../styles/mixins';

.home-page {
  &__splash,
  &__about,
  &__offer,
  &__news,
  &__references,
  &__contact,
  &__newsletter {
    position: relative;
    background-position: center center;
    overflow: hidden;
  }

  /* Splash */

  &__splash {
    background-image: url('../../images/splash.jpg');
    background-size: cover;
    background-attachment: fixed;
    padding-top: 54px;
    z-index: 9;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.7);
      z-index: -9;
    }
  }

  /* Offer */

  &__offer {
    background-image: url('../../images/bg-layer-qube-min.png');
    padding: 54px;

    .offer-wrap {
      @include flex(row, center, space-between);
      flex-wrap: wrap;
      padding: 60px 0;
    }
  }

  /* About */

  &__about {
    background-image: url('../../images/bg-layer-qube-min.png');
    padding: 0rem 0;

    .about-wrapper {
      @include flex(row, center, space-between);
      padding: 2rem 0;
      flex-wrap: wrap;

      @include sm-max {
        flex-direction: column;
      }
    }
  }

  &__contact {
    height: 35vh;
  }

  &__newsletter {
    background-color: var(--color-green-dark);

    .newsletter-section {
      @include flex(row, center, space-between);
      padding: 50px;

      @include md-max {
        @include flex(column-reverse, center, space-around);
      }
    }
  }
}
